export default {
	
	data() {
		return {
			disabled:true,
			emailNum: '',
			password: '',
			opcity: 0.5
		}
	},
	onLoad() {

	},
	onReady() {

	},
	methods: {
		//登录
		login() {
			this.disabled = true
			uni.request({
				url: this.baseUrl + "/member-login",
				data: {
					Email: this.emailNum,
					Password:this.password
				},
				method: "POST",
				success: (res) => {
					console.log(res)
					if (res.data.status == 1) {
						uni.setStorageSync("token",res.data.data)
						console.log(uni.getStorageSync('token'))
						this.disabled = true
						uni.redirectTo({
							url:"../index/index"
						})
					} else {
						this.disabled = false
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
				}
			})
		},
		change(e) {
			console.log(e.detail.value.length)
			if (e.detail.value.length >= 3) {
				this.opcity = 1
				this.disabled = false
			} else {
				this.opcity = 0.5
				this.disabled = true
			}
		},
		jumpToForgetPassword(){
			uni.navigateTo({
				url:"./forgetPassword"
			})
		}
	}
}
